@use 'base/variables' as v;
@use 'base/mixins' as m;

.tabs{

    @include m.tablet{
        display: flex;
    }

    button{
        background-color: v.$azul;
        border: none;
        color: v.$blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        text-transform: uppercase;
        font-weight: bold;
        margin: 1rem 1rem;
        border-radius: 2rem;
        transition: all .3s;
        &:hover{
            cursor: pointer;
            background-color: darken(v.$azul, 19%);
        }
        &.actual{
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}