@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app{

    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }
}

.imagen{
    height: 20rem;
    background-image: url('../img/barber.jpg');
    background-size: cover;
    background-position: center center;
    @include m.tablet{
        height: auto;
    }
  
}
.app{
    width: 95%;
    margin: 0 auto;
    padding: 3rem 0;
    @include m.tablet{
        overflow: scroll;
    }
}
.barra{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 5rem;
    place-items: center;
   
    p{
        border: 1px solid v.$blanco;
        border-radius: 5px;
        padding: 1rem;
    }

    .boton{
        width: 45rem;
        @include m.boton(v.$azul, v.$blanco)
    }
}

.barra-servicios{
   
    width: 35rem;
    margin: 0 auto;
    @include m.tablet{
        display: flex;
        justify-content: space-between;
        width: auto;
    }
    .boton{
        margin: 0 auto 1rem auto; ;
    }
}