@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios{
    display: grid;
    gap: 2rem;

    @include m.tablet{
        @include m.grid(2, 1rem)
    }
    
    .servicio{
        background-color: v.$blanco;
        padding: 1rem;
        border-radius: 2rem;
        cursor: pointer;
        transition: all .3s;
        text-align: center;
        &:hover{
            transform: scale(1.05);
        }
        &.seleccionado{
            background-color: v.$azul;
            p{
                color: v.$blanco;
            }
        }
        
        p{
            color: v.$negro;
            margin: 1rem;
        }
        .precio-servicio{
            color: v.$azul;
            font-weight: bold;
        }
    }

}