@use 'base/variables' as v;
@use 'base/mixins' as m;

.seccion{
    padding: 5rem 0;
    display: none;

    .text-center{
        text-align: center;
    }
}
.mostrar{
    display: block;
}
.ocultat{
    display: none;
}

.citas{
 
    list-style: none;
   
    li{
        border:  5px solid v.$azul;
        border-radius: 10px; 
        padding: 3rem;
        margin-bottom: 2rem;
        transition: all 0.3s;
        &:hover{
            transform: scale(1.05);
        }
        span{
            font-weight: bold;
            color: v.$azul;
        }
    }
}
.servicios{
    @extend .citas;
    .acciones{
        margin: 0;
    }
    @include m.tablet{
        .boton,
    .boton-eliminar{
        margin: 0;
    }
    }
}
