@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    width: 100%;
    margin-top: 3rem;
}
.campo{
    
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label{
        flex: 0 0 10rem;
    }
    input{
        flex: 1;
        border: none;
        padding: calc(v.$separacion/4);
        border-radius: 10px;

        &:disabled{
            background-color: lighten(v.$negro, 45%);
            color: v.$blanco;
           
        }
    }
}