@use 'base/variables' as v;

.alerta{
padding: calc(v.$separacion/3);
color: v.$negro;
width: 100%;
display: block;
text-align: center;
margin-bottom:2rem ;
transition: all .3s;
   &.error{
    background: rgb(255,0,0);
    background: linear-gradient(152deg, rgba(255,0,0,1) 0%, rgba(255,118,118,1) 25%, rgba(255,90,90,1) 50%, rgba(255,117,117,1) 75%, rgba(255,0,0,1) 100%); 
    border-radius: 1rem;
   }
   &.exito{
    background: rgb(26,255,0);
    background: linear-gradient(152deg, rgba(26,255,0,1) 0%, rgba(192,255,178,1) 25%, rgba(93,255,90,1) 50%, rgba(204,255,190,1) 75%, rgba(0,255,25,1) 100%); 
    border-radius: 1rem;
   }
}
